.video-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    /* Puoi adattare questo colore se nel resto del sito usi variabili specifiche */
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .video-modal-content {
    position: relative;
    /* Usa la variabile di colore per il background se presente, ad es. var(--card-bg) o var(--bg-color) */
    background: var(--card-bg);
    padding: 1rem;
    max-width: 700px; /* Dimensione massima ridotta */
    width: 90%;
    border-radius: 8px;
  }
  
  .video-modal-close {
    position: absolute;
    top: 10px;
    right: 10px;
    background: transparent;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    z-index: 2; /* Assicura che il pulsante sia sopra l'iframe */
    color: var(--text-color);
  }
  
  .video-container {
    position: relative;
    padding-bottom: 56.25%; /* Mantiene il rapporto 16:9 */
    height: 0;
  }
  
  .video-container iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0;
  }
  